html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 1.6666666667rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenedor-anuncio {
  max-width: 75%;
  margin: 0 auto;
}

.contenedor-nuestro-blog {
  max-width: 70%;
  margin: 0 auto;
}

.contenedor-entrada {
  max-width: 70%;
  margin: 0 auto;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

@media (min-width: 768px) {
  .alinear-derechaLg {
    display: flex;
    justify-content: flex-end;
  }
}

@media (min-width: 768px) {
  .alinear-izquierdaLg {
    display: flex;
    justify-content: flex-start;
  }
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear-izquierda {
  display: flex;
  justify-content: flex-start;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  display: block;
  text-align: center;
  border: solid #E08709 0.2rem;
}
.boton-amarillo:hover {
  background-color: transparent;
  color: #E08709;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  display: block;
  text-align: center;
  border: solid #71B100 0.2rem;
}
.boton-verde:hover {
  background-color: transparent;
  color: #71B100;
}

.boton-verde-adm {
  margin-top: 0.5rem;
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  display: block;
  text-align: center;
  border: solid #71B100 0.2rem;
}
.boton-verde-adm:hover {
  background-color: transparent;
  color: #71B100;
}
@media (min-width: 768px) {
  .boton-verde-adm {
    background-color: #71B100;
    color: #FFFFFF;
    font-weight: 700;
    text-decoration: none;
    padding: 1rem 3rem;
    display: inline-block;
    text-align: center;
    border: solid #71B100 0.2rem;
  }
  .boton-verde-adm:hover {
    background-color: transparent;
    color: #71B100;
  }
}

.boton-amarillo-il {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  display: inline-block;
  text-align: center;
  border: solid #E08709 0.2rem;
}
.boton-amarillo-il:hover {
  background-color: transparent;
  color: #E08709;
}

.boton-verde-il {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  display: inline-block;
  text-align: center;
  border: solid #71B100 0.2rem;
}
.boton-verde-il:hover {
  background-color: transparent;
  color: #71B100;
}

.btn-editar, .btn-borrar {
  color: #000;
  padding: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: none;
}

.btn-secondary:hover {
  background-color: rgba(177, 171, 171, 0.836);
}

.btn-editar:visited {
  color: #000;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #FFFFFF;
}
.dark-mode .dark-mode-boton {
  color: #7b7b7b;
}
.dark-mode .anuncio {
  background-color: #333333;
  border-color: #212121;
}
.dark-mode .anuncio .iconos-caracteristicas p {
  color: black;
}
.dark-mode .formulario fieldset, .dark-mode .formulario legend {
  color: #FFFFFF;
  margin-top: 1rem;
}
.dark-mode table.propiedades td {
  color: #FFFFFF;
  font-weight: 300;
  background-color: #333333;
}

.no-webp .index {
  background-image: url(../img/header.jpg);
}

.webp .index {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding-bottom: 2rem;
}
.header.index {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.index {
    min-height: 60rem;
  }
}
@media (min-width: 768px) {
  .header.index .contenido-header {
    min-height: 60rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  text-align: left;
  font-size: 4rem;
  font-weight: 300;
  line-height: 1.5;
  margin-left: 1rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    max-width: 60rem;
    line-height: 2;
  }
}

.footer {
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .contenedor-footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem;
  }
}
.footer .copyright {
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 200;
  text-align: center;
  margin: 0;
  order: 1;
  margin-bottom: 0.5rem;
}
@media (min-width: 768px) {
  .footer .copyright {
    order: -1;
    margin-bottom: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    align-items: center;
    padding-top: 2rem;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.2s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion p {
  margin: 0;
  text-align: center;
  font-size: 2rem;
}
.navegacion p:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .navegacion p {
    margin-left: 1rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 300;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.nav_footer {
  padding: 0.5rem;
}
.nav_footer a {
  margin-left: 2rem;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 200;
}
.nav_footer:last-of-type {
  margin-left: 0;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 3rem;
}
.mobile-menu img:hover {
  cursor: pointer;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

.dark-mode-boton {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 5rem;
}
.icono h3 {
  text-transform: uppercase;
  font-size: 2.5rem;
  font-weight: 300;
}
.icono p {
  font-weight: 300;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 1rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr auto auto auto;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio p {
  font-size: 2rem;
  font-weight: 300;
}
.anuncio .precio {
  color: #71B100;
  font-weight: 400;
  font-size: 1.8rem;
}

.iconos-caracteristicas {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  display: flex;
}
.iconos-caracteristicas li img {
  margin: 0 0.5rem;
  max-height: 3rem;
}
.iconos-caracteristicas li p {
  font-weight: 700;
  font-size: 2.2rem;
}

.img-anuncio {
  max-width: 60rem;
  display: inline-block;
}

.text-center {
  text-align: center;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.web-p .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-image: url("../img/encuentra.jpg");
  background-position: center center;
  background-size: cover;
  padding: 4rem 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  color: #FFFFFF;
}
.imagen-contacto h2 {
  font-size: 4rem;
}
.imagen-contacto p {
  color: #FFFFFF;
  font-weight: 300;
  font-size: 2.5rem;
}
.imagen-contacto .contenido-contacto {
  padding: 1rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.seccion-inferior h3 {
  font-size: 3.5rem;
  font-weight: 300;
}

.blog {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .blog {
    row-gap: 2rem;
  }
}

.imagen-entrada {
  display: flex;
  align-items: center;
}

@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #000000;
}
.texto-entrada h4 {
  margin-top: 0.5rem;
  margin-bottom: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 0.5rem;
}
.texto-entrada span {
  color: #E08709;
}
.texto-entrada p {
  margin: 1rem 0;
  font-weight: 300;
}
@media (min-width: 768px) {
  .texto-entrada h4 {
    margin-top: 0;
  }
}

.testimonial {
  background-color: #71B100;
  padding: 2rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 300;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
  top: -1.5rem;
}
.testimonial p {
  color: #FFFFFF;
  font-weight: 400;
  text-align: right;
  margin-right: 1rem;
}

.texto-nosotros p {
  font-weight: 300;
}
@media (max-width: 768px) {
  .texto-nosotros {
    max-width: 95%;
    margin: 0 auto;
  }
}

@media (min-width: 768px) {
  .nosotros-display {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.nosotros-display .img-nosotros {
  display: flex;
  align-items: center;
}

.contenido-nosotros h2 {
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .contenido-nosotros h2 {
    margin-top: 0;
  }
}
.contenido-nosotros p {
  line-height: 1.3;
  margin: 0 0 1rem 0;
  text-align: justify;
}
.contenido-nosotros p:last-of-type {
  margin-bottom: 0;
}

.fondo-contacto {
  background-image: url(../img/destacada.webp);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 30rem;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 400;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.boton-verde {
  margin-top: 1rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 300;
  text-transform: uppercase;
  margin: 1rem 0;
  font-size: smaller;
}
.alerta.error {
  background-color: rgb(152, 24, 24);
}
.alerta.error p {
  color: #FFFFFF;
  margin: 0;
  font-size: 1.2rem;
  font-weight: 400;
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  text-align: center;
  border-collapse: separate;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades td {
  border: 0.1rem solid #71B100;
}
table.propiedades .imagen-tabla {
  width: 10rem;
  display: inline;
  margin: 0.5rem;
}
table.propiedades p {
  font-size: 1.6rem !important;
}

.imagen-small {
  width: 20rem;
}

.autoPrecio {
  font-weight: 300;
}